@import "../style/scss/common";

.section-form {
  grid-row: 1/4;
  display: flex;
  flex-direction: column;
  gap: 24px;
  max-width: 640px;
  padding: 60px 0;
  margin: 0 auto;
  &__header {
    display: flex;
    justify-content: center;
    padding-bottom: 16px;
    .link-home {
      display: flex;
    }
  }
  &__form {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  &__label {
    display: block;
    font-family: Pretendard, sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 21.6px;
    margin-bottom: 16px;
  }
  &__input-box {
    position: relative;
    .chk-visibility:checked + .visibility-off {
      background-position: -5px -34px;
    }
    .visibility-off {
      position: absolute;
      top: 50%;
      right: 16px;
      transform: translateY(-50%);
    }
  }
  &__btn {
    width: 100%;
  }
  &__info {
    .content {
      text-align: center;
      font-family: Pretendard, sans-serif;
      font-size: 15px;
      font-weight: 500;
      line-height: 17.9px;
    }
    .link {
      text-decoration: underline;
      color: #3182f6;
    }
  }
}

.section-other {
  display: flex;
  padding: 16px 23px;
  justify-content: space-between;
  align-items: center;
  background: #e6f2ff;
  border-radius: 8px;

  &__tit {
    font-family: Pretendard;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }
  &__content {
    display: flex;
    gap: 16px;
    align-items: center;
    .link {
      display: flex;
    }
  }
}
